import React, {useEffect, useMemo, useState} from "react";
import {Button, List} from 'antd';
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'

function FirstLevelNavigation(props) {
  const {setTwoList, setOneTitle, setActiveTwo, history, rootState, location} = props;

  const [activeIndex, setActiveIndex] = useState(-1);
  const memoList = useMemo(() => ({
    rootState,
    renderList: rootState.filter(item => !item.title.includes('.md'))
  }), [rootState]);
  useEffect(() => {
    memoList.renderList.forEach((item, index) => {
      let splitPathName = location.pathname.split('/');
      let pathName1 = '';
      if (splitPathName.length === 3) {
        pathName1 = splitPathName[splitPathName.length-2]
      }else if (splitPathName.length === 2) {
        pathName1 = splitPathName[splitPathName.length-1]
      }else {
        pathName1 = ''
      }
      if (pathName1 && item.uid.includes(pathName1)) {
        setActiveIndex(index);
      }
    });
  }, [memoList.renderList, location.pathname]);
  return <>
    <List
      itemLayout="horizontal"
      className={'list'}
      dataSource={memoList.renderList}
      renderItem={(item, index) => (
        <List.Item
          className={index === activeIndex ? 'active' : 'common'}
          style={{'--icon':item.icon,'--color':'#cccccc'}}
          onClick={() => {
            history.push(`/${item.uid.split('-')[0]}/${item.children[0].uid.split('-')[0]}`);
            // 处理路由
            setTwoList(item.children);
            setOneTitle(item.title);
            setActiveIndex(index);
            setActiveTwo(index);
          }}>
          <img className={'icon_one_title'} src={item.icon} alt="icon"/>
          <List.Item.Meta
            className={'one_title'}
            title={<span
              className={`title_text oneTitle`}
              id={index === activeIndex ? 'activeFont' : 'commonFont'}
            >{item.title}</span>}
          />
        </List.Item>
      )}
    />
  </>;
}

const mapStateToProps = state => {
  return {
    rootState: state.rootReducers
  };
};
export default connect(mapStateToProps)(withRouter(FirstLevelNavigation));
